}

.div.onGroomClick, onBrideClick {
    display: flex;
    align-items: center;
    /* 팝업이 열릴때 스르륵 열리는 효과 */
    -webkit-animation: bg-show 0.3s;
            animation: bg-show 0.3s;
}
